select::-ms-expand {
  display: none;
}
/* Custom Select */
.Select_select__3l9cd {
  position: relative;
  display: flex;
  width: 20em;
  height: 3em;
  line-height: 3;
  background: #e05d5d;
  overflow: hidden;
  border-radius: 0.25em;
  margin: 1rem;
}
select {
  flex: 1 1;
  padding: 0 1rem;
  color: #fff8e5;
  cursor: pointer;
}
/* Arrow */
.Select_select__3l9cd::after {
  content: "\25BC";
  position: absolute;
  top: 0;
  right: 0;
  padding: 0 1em;
  background: #5b5b5b;
  cursor: pointer;
  pointer-events: none;
  transition: 0.25s all ease;
}
/* Transition */
.Select_select__3l9cd:hover::after {
  color: #000000;
}

